.home-page {
  width: 100%;
  height: 100vh;
  background-image: url('https://img.freepik.com/free-vector/frame-with-dogs-vector-white-background_53876-127700.jpg?t=st=1733176234~exp=1733179834~hmac=f6468f1255fbf5d29786ba2907ccc09ec066642603fb47e19bac478c9a3c4976&w=1060');
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;

  .hero-section {
    position: relative;
    text-align: center;
    margin-bottom: 40px;
    background-size: cover;
    background-position: center;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;

    .hero-overlay {
      background: rgba(0, 0, 0, 0.5);
      padding: 20px;
      border-radius: 10px;
      color: #fff;

      h1 {
        font-size: 3rem;
        margin-bottom: 20px;
      }

      p {
        font-size: 1.5rem;
        margin-bottom: 20px;
      }

      button {
        font-size: 1.2rem;
        padding: 10px 20px;
      }
    }
  }

  .about-section,
  .services-section {
    margin-bottom: 40px;
    text-align: center;

    h2 {
      margin-bottom: 20px;
    }

    p {
      font-size: 1.2rem;
    }
  }

  .services-section {
    .MuiCard-root {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      h3 {
        margin-bottom: 10px;
      }

      p {
        flex-grow: 1;
      }
    }
  }

  @media (max-width: 768px) {
    padding: 10px;

    .hero-section {
      height: auto;

      .hero-overlay {
        padding: 10px;

        h1 {
          font-size: 2rem;
        }

        p {
          font-size: 1rem;
        }

        button {
          font-size: 1rem;
          padding: 5px 10px;
        }
      }
    }

    .about-section,
    .services-section {
      margin-bottom: 20px;

      h2 {
        font-size: 1.5rem;
      }

      p {
        font-size: 1rem;
      }
    }
  }

  @media (max-width: 480px) {
    padding: 5px;

    .hero-section {
      height: auto;

      .hero-overlay {
        padding: 5px;

        h1 {
          font-size: 1.5rem;
        }

        p {
          font-size: 0.8rem;
        }

        button {
          font-size: 0.8rem;
          padding: 5px;
        }
      }
    }

    .about-section,
    .services-section {
      margin-bottom: 10px;

      h2 {
        font-size: 1.2rem;
      }

      p {
        font-size: 0.8rem;
      }
    }
  }
}